import React from 'react'
import "../Css/Newsfeed.css"
import Post from './Post'
import { Row , Col } from 'react-bootstrap'
import PostHadhees from './PostHadhees'
function Newsfeed() {
    return (
        
        <Row className="testnew justify-content-sm-center">
          <Col sm={12}> <h1>CRE NOTES</h1></Col>
           <Post></Post>
           <Col sm={12}><h1>HADHEES CLASS</h1></Col>
           <PostHadhees></PostHadhees>
           
        </Row>
    )
}

export default Newsfeed
