import React from 'react'
import "../Css/Post.css"
import {Button, Row, Col } from 'react-bootstrap'

import { useState } from 'react'
import { useEffect } from 'react'
import axios from 'axios'

function PostHadhees() {
  const [state, setstate] = useState([])
  useEffect(() => {
    axios.get('https://sheetdb.io/api/v1/lcxiifqcqa3mk').then((respone) => {
      console.log(respone.data)
      setstate(respone.data)

    })

  }, [])

  return (
    <div className="Post">



      {state.map((obj) => {
        return (




          <Row className="Post1">
            <Col>

              <Row>
                <img className="propic" src="./Images/crelogo.png" alt="" /><h4 className="proname">CRE STUDENTS</h4><h4 className="date">{obj.date}</h4></Row>
              <h3>{obj.heading2}<Button  variant="primary" size="sm" href={obj.link2}>  Download</Button></h3>
              <p>{obj.hadhees}</p>
            </Col>
          </Row>




        )

      })}

    </div>

  )
}





export default PostHadhees
