import React, { useState } from 'react';
import { Row , Col } from 'react-bootstrap'
import "../Css/MenuButton.css"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
function MenuButton() {
  const [page, setPage] = useState([])
    return (
      <Router>
        <Row className="justify-content-sm-center">
          <Col xs={3} className="button1" onClick={() => { setPage("CRE NOTES")}}><Link to="/notes"><div>CRE NOTES</div></Link></Col>
          <Col xs={3} className="button2" onClick={() => { setPage("Hadhees CLASS")}}><Link to="/hadeesclass"><div>Hadhees CLASS</div></Link></Col>
          {/* <Col xs={3} className="button3" onClick={() => { setPage("Quiz")}}><div>Quiz</div></Col> */}
        </Row>
        </Router> 
    )
}

export default MenuButton
