function Navibar() {
    return (
        <Row className="testnavi justify-content-sm-center">
            
            <div className="box"><img className="crelogo" src="./Images/crelogo.png" alt="" /></div>
            <div className="navilogin"><Button  variant="primary" size="sm">Login</Button><img className="propic" src="../Images/crelogo.png" alt="" /></div>
            
        </Row>
       
    )
}

export default Navibar
